body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  max-width: 800px;
  margin: auto;
}

.detail_img {
  width: 100vw;
}

.ant-row {
  justify-content: center;
}

.ant-card-cover {
  overflow: hidden;
}

.ant-card-cover img {
  height: 400px;
  object-fit: cover;
}

@media only screen and (max-width: 800px) {
  .ant-card-cover {
    width: 40vw;
    height: 40vw;
    overflow: hidden;
  }

  .ant-card-cover img {
    width: 40vw;
    height: 40vw;
    object-fit: cover;
  }
}

.ant-card-bordered {
  margin: auto;
}

.detail_content {
  margin: 20px;
}

.detail_content button {
  margin: auto;
}
